import $ from "jquery";
import SumoSelect  from 'sumoselect';

window.initSelect = function(){

    $('.jsSelect').each(function(){
        let thisSelect = $(this);
        let placeholder = thisSelect.attr('placeholder');
    
        thisSelect.SumoSelect({
            placeholder: placeholder,
            forceCustomRendering: true,
            captionFormat:'{0} ', 
            captionFormatAllSelected:'{0}  !',
        });
    })
}

document.addEventListener('DOMContentLoaded', function(){
    window.initSelect()
})
